<template>
  <div class="page">
    <!-- 轮播图片区域 -->
    <div class="banner">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img src="../config/img/banner.png" alt />
        </div>
      </div>
    </div>

    <!-- 产品名称区域 -->
    <div class="product_name">
      <h1>寻探客 男鞋2020夏季透气板鞋超火ING时尚运动休闲鞋网红同款百搭潮鞋子男高帮鞋学生帆布小白鞋子男 白灰（冰丝布面） 40</h1>
    </div>

    <!-- 抢购及时区域 -->
    <div class="time">
      <i class="end_t">距离结束</i>
      <div class="back">
        <h2>12 小时 59 分 21 秒</h2>
      </div>
    </div>

    <!-- 产品介绍 -->
    <div class="product_info">
      <p>随着拼夕夕和云集的上市，社交电商模式也开始被阿里、京东等电商平台重视、也纷纷推出的不同模式的社交电商平台，如阿里的淘小铺、京东的享橙，以及小米的有品有鱼。</p>
      <p>不同的新的社交电商平台和各种玩法的社交零售模式层出不穷，就如马云曾经说的电子商务只是刚刚开始，商业模式在市场竞争机制中会不断的被颠覆，除了社交+电商模式，社交+本地服务模式（本地服务类社交电商）也即将带来巨大商家。</p>
    </div>

    <!-- 图片展示区域 -->
    <div class="product_img">
      <div class="img_box" v-for="(item,index) in 5" :key="index">
        <img src="../config/img/banner.png" alt />
      </div>
    </div>

    <!-- 一键支付按钮区域 -->
    <div class="payBtn" @click="dialogVisible = true">一 键 抢 购</div>

    <!-- 表单弹出区域 -->
    <el-dialog
      title="立即抢购"
      custom-class="dialog"
      :visible.sync="dialogVisible"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <!-- 提交表单区域 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="formStyle"
      >
        <el-form-item label="收件人" prop="name" width="50">
          <el-input class="name" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="收件地址" prop="site">
          <el-input class="site" v-model="ruleForm.site"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input class="phone" v-model="ruleForm.phone"></el-input>
          <button class="getcode" @click="getPhoneCode">获取验证码</button>
        </el-form-item>
        <el-form-item label="验证码" prop="phoneCode">
          <el-input class="vcode" v-model="ruleForm.phoneCode"></el-input>
        </el-form-item>
        <el-form-item label="选择颜色">
          <el-select v-model="color" placeholder="请选择">
            <el-option
              v-for="(item,index) in this.colorArr"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择尺码">
          <el-select v-model="size" placeholder="请选择">
            <el-option
              v-for="(item,index) in sizeArr"
              :key="index"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择数量">
          <el-input-number class="num" v-model="num" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item label="备注">
          <el-input class="remark" v-model="ruleForm.remark"></el-input>
        </el-form-item>
      </el-form>

      <!-- 底部确定按钮区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Swiper from "swiper";
export default {
  name: "index",
  data() {
    return {
      appId: "",
      dialogVisible: false,
      num: "1",
      color: "",
      colorArr: [
        {
          value: "选项1",
          label: "红色"
        },
        {
          value: "选项2",
          label: "黑色",
          disabled: true
        },
        {
          value: "选项3",
          label: "白色"
        },
        {
          value: "选项4",
          label: "绿色"
        },
        {
          value: "选项5",
          label: "紫色"
        }
      ],
      size: "",
      sizeArr: [
        {
          value: "选项1",
          label: "38"
        },
        {
          value: "选项2",
          label: "39",
          disabled: true
        },
        {
          value: "选项3",
          label: "40"
        },
        {
          value: "选项4",
          label: "41"
        },
        {
          value: "选项5",
          label: "42"
        },
        {
          value: "选项1",
          label: "38"
        },
        {
          value: "选项2",
          label: "39",
          disabled: true
        },
        {
          value: "选项3",
          label: "40"
        },
        {
          value: "选项4",
          label: "41"
        },
        {
          value: "选项5",
          label: "42"
        }
      ],
      ruleForm: {
        name: "",
        site: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        remark: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入收件人姓名", trigger: "blur" },
          { min: 2, max: 5, message: "请输入姓名或昵称", trigger: "blur" }
        ],
        site: [
          { required: true, message: "请输入收件地址", trigger: "blur" },
          { trigger: "blur" }
        ],
        phone: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { min: 11, max: 11, message: "请输入正确的手机号码", trigger: "blur" }
        ],
        phoneCode: [
          { required: true, message: "请输入验证码", trigger: "blur" },
          { min: 6, max: 6, message: "请输入6位验证码", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    var swiper = new Swiper(".banner", {
      // loop : true,
    });
  },
  methods: {
    getPhoneCode() {
      alert(1);
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert(
            "姓名>" +
              this.ruleForm.name +
              "地址>" +
              this.ruleForm.site +
              "电话>" +
              this.ruleForm.phone +
              "验证码>" +
              this.ruleForm.phoneCode +
              "颜色>" +
              this.color +
              "尺码>" +
              this.size +
              "数量>" +
              this.num
          );
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.num {
  width: 200px;
}

::-webkit-scrollbar {
  width: 0 !important;
}
::-webkit-scrollbar {
  width: 0 !important;
  height: 0;
}
.page {
  width: 100%;
  height: 100%;
  background-color: #f4f4f4;
}
.banner {
  position: relative;
  width: 100%;
  height: 550px;
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
}
.banner img {
  width: 100%;
}

.product_name {
  width: 100%;
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
}
.product_name h1 {
  color: #86868b;
  margin-top: 20px;
  padding: 20px 50px;
  line-height: 42px;
  text-align: center;
  font-size: 30px;
}
.time {
  margin-top: 20px;
  padding: 30px 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
}
.end_t {
  display: block;
  width: 100%;
  text-align: center;
  color: #999;
  font-size: 22px;
  margin-bottom: 10px;
}
.back {
  width: 50%;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.8);
}
.back h2 {
  font-size: 30px;
  display: block;
  padding: 20px 0;
  color: #fff;
  text-align: center;
}
.product_info {
  margin-top: 20px;
  width: 100%;
  padding: 30px 0;
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
}
.product_info p {
  font-size: 26px;
  line-height: 38px;
  padding: 10px 50px;
  color: #999;
}
.product_img {
  margin-top: 20px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
  padding-bottom: 130px;
}
.img_box {
  border-bottom: 1px solid #eee;
}
.img_box img {
  width: 100%;
}

.payBtn {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 130px;
  color: #fff;
  text-align: center;
  font-size: 38px;
  line-height: 100px;
  font-weight: 700;
  background-color: rgba(0, 0, 0, 0.8);
}

.getcode {
  margin-left: 20px;
  border: none;
  color: #fff;
  padding: 5px 20px;
  background-color: rgba(0, 0, 0, 0.8);
}
</style>



